import React, { Component, Suspense } from 'react';
import { FontAwesomeIcon } from '../../../Assets/AssetsHelper'
import { Dropdown, Navbar, Button, Image } from 'react-bootstrap';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import { Cookies } from 'react-cookie';
import { IS_IN_IFRAM, OPEN_INTERCOM, showSalesModal } from '../../../utils';
import { DOMAIN_NAME, LOGOBRAND, MediaBaseUrl } from '../../../actions/api';
import { updateSalesModal } from '../../../actions/loginActions';
import UpdateTimeModal from '../Main/CommonComponents/UpdateTimeModal';
import { LANGUAGE_SUPPORTS, PRODUCT_PLANS } from '../../../actions/constants';
import { changeAppLanguage } from '../../../actions/appsMoreAction';
const UserDropdown = React.lazy(() => import('./UserDropdown'));
const Notifications = React.lazy(() => import('../Main/CommonComponents/Notifications'));
const cookies = new Cookies();

const DropdownItem = (props) => {
       return <Dropdown.Item {...props}>
              <FontAwesomeIcon type={'solid'} icon={props.iconText} />
              {props.text}
       </Dropdown.Item>
}

class Header extends Component {
       constructor(props) {
              super(props);
              this.state = { isActiveAside: false, appThemeSkin: cookies.get('appThemeSkin') ? cookies.get('appThemeSkin') == 'light' ? true : false : true, isNotification: false, upTimeModal: false };
              const htmlTag = document.getElementsByTagName("html")[0];
              if (cookies.get('appThemeSkin')) {
                     htmlTag.setAttribute("data-theme", cookies.get('appThemeSkin'));
              }
              htmlTag.setAttribute("data-product", DOMAIN_NAME);                     
       }

       setMode = () => {
              const htmlTag = document.getElementsByTagName("html")[0];
              this.setState({ appThemeSkin: !this.state.appThemeSkin },() => {
                     htmlTag.setAttribute("data-theme", this.state.appThemeSkin ? 'light' : 'dark')
                     cookies.set("appThemeSkin", this.state.appThemeSkin ? 'light' : 'dark', { path: '/' })
                     htmlTag.setAttribute("data-product", DOMAIN_NAME);
              });
       };

       onNotificationToggle = () => this.setState({ isNotification: !this.state.isNotification })
       render() {
              const { navigate, authorized, notificationList, updateSalesModal } = this.props
              const { isNotification, appThemeSkin } = this.state
              const notifications_count = notificationList.filter((a) => a.status == 0).length
              const userData = authorized?.userData?.user_data ?? {}
              const updatesIntervalCron = userData?.UserRule?.updatesIntervalCron ?? "";
              const advPlan = PRODUCT_PLANS.thirdPaid.includes(userData?.UserPlan?.planId ?? "");
              const pageContent= window?.APP_CONTENT ?? {};
              const { common, help, header } = pageContent;
              const isInIframe = IS_IN_IFRAM();

              return <Navbar className="header-main f-center py-0 flex-nowrap" expand="lg">
                     <div className="app-logo f-center flex-shrink-1 border-end">
                            <Link to={"/"} className="f-center header_logo" title={DOMAIN_NAME}>
                                   <span className="d-flex w-100">
                                          <Image src={LOGOBRAND} width="34" height="34" alt={DOMAIN_NAME} title={DOMAIN_NAME} fluid/>
                                   </span>
                            </Link>
                     </div>
                     <div className="app-container f-between flex-grow-1 h-100">
                            <div className="d-flex flex-grow-1 w-100">
                                   <div className="d-lg-none d-flex align-items-center justify-content-center mob_toggle_icon me-3">
                                          <Button id="main_menu_toggle" variant='icon' size='sm' className="p-0 custom_burger_icon bg-tansparent" aria-label='toggle' title='toggle'>
                                                 <span></span>
                                                 <span></span>
                                                 <span></span>
                                          </Button>
                                   </div>
                                   {/* {/\/(moderation|addfeed|managefeed|more-apps|manual-upload|reviewhub|autopilot)/.test(window.location.pathname) ? <ContentSwitcher header={true}/> : ''} */}
                            </div>
                            {!window.location.pathname.includes("onboarding") ? <div className="header-menu d-flex align-items-center justify-content-end w-xs-100 h-100">
                                   <div className="d-flex align-items-center h-100 flex-shrink-0">
                                          {!userData?.UserPlan?.planId?.includes('39') && userData?.collaborator == '' && (
                                                 <Link to={advPlan ?`#` :`/price`}  onClick={() => advPlan ? updateSalesModal(showSalesModal(userData)) : navigate(`/price`)} className="btn btn-primary text-nowrap btn-md mx-1 d-none d-sm-flex" title="Upgrade now!" style={{ minWidth: 34 }}>
                                                        {advPlan ? common?.talk_to_sales ?? "" : common?.upgrade_now ?? ""}
                                                 </Link>
                                          )}

                                          <div className="topbar-item ms-2">
                                                 <a className="btn btn-secondary p-1 pe-2 border-0" onClick={() => this.setState({ upTimeModal: true })}>
                                                        <span className="svg-icon svg-icon-lg text-muted d-inline-block">
                                                               <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
                                                                      <g id="Group_4428" data-name="Group 4428" transform="translate(10979 -4712)">
                                                                             <rect id="Rectangle_2094" data-name="Rectangle 2094" width="24" height="24" transform="translate(-10979 4712)" fill="none" opacity="0"></rect>
                                                                             <path id="Path_5220" data-name="Path 5220" d="M12,2.577A9.423,9.423,0,1,0,21.423,12,9.433,9.433,0,0,0,12,2.577Zm0,17.042A7.619,7.619,0,1,1,19.619,12,7.628,7.628,0,0,1,12,19.619Z" transform="translate(-10979 4712)" fill="#545454"></path>
                                                                             <path id="Path_5221" data-name="Path 5221" d="M12.142,7.232a.9.9,0,0,0-.629-.287h-.024a.915.915,0,0,0-.908.929v4.7a.9.9,0,0,0,.9.9H15.2a.911.911,0,0,0,.9-.872.9.9,0,0,0-.749-.911l-2.969-.033V7.883A.9.9,0,0,0,12.142,7.232Z" transform="translate(-10979 4712)" fill="#545454"></path>
                                                                      </g>
                                                               </svg>
                                                        </span>
                                                        <span className="ps-1 font-weight-bold d-none d-md-inline updateTimeVal font-size-sm text-muted align-middle">{updatesIntervalCron ? `${updatesIntervalCron}` : ""}</span>
                                                 </a>
                                          </div>

                                          {!isInIframe ? <Button variant="icon" size='sm' className="fs-6 border-0 ms-sm-1" aria-label='Mode' onClick={this.setMode}>
                                                 <i className={`fa-duotone fa-solid fa-${appThemeSkin ? 'moon' : 'sun-bright'}`}></i>
                                          </Button> : null}

                                          {!isInIframe ? <Dropdown className='h-100 f-center d-none d-sm-flex' align="end">
                                                 <Dropdown.Toggle variant='icon' size='sm' className='fs-6 border-0 arrow_disabled ms-sm-1' aria-label='Support'>
                                                        <i className="fa-duotone fa-solid fa-circle-question"></i>
                                                 </Dropdown.Toggle>
                                                 <Dropdown.Menu>
                                                        <DropdownItem iconText="comments" text={help?.chat ?? ""} onClick={OPEN_INTERCOM} />
                                                        <DropdownItem iconText="phone" text={help?.call ?? ""} onClick={() => updateSalesModal(showSalesModal(userData))} />
                                                        <DropdownItem iconText="envelope" text={help?.email ?? ""} onClick={() => window.open(`mailto:support@${DOMAIN_NAME}.com`)} />
                                                        <DropdownItem iconText="book-open-cover" text={help?.knowledgeBase ?? ""} href={`https://${DOMAIN_NAME}.com/support`} target='_blank' />
                                                 </Dropdown.Menu>
                                          </Dropdown> : null}

                                          {!isInIframe ? <Dropdown className='f-center d-flex bg-transparent mt-1 mt-sm-0' align="end">
                                                 <Dropdown.Toggle size='sm' className='border-0 arrow_disabled text-muted ms-sm-1 rounded bg-secondary font-size-sm px-2' aria-label='Support'>
                                                        <FontAwesomeIcon type={'regular'} icon={"globe"} />
                                                        <span className="me-1 text-uppercase">{userData?.user?.langSelect ?? ""}</span>
                                                        <FontAwesomeIcon iconClass="me-0" type={'solid'} icon={"chevron-down"} />
                                                 </Dropdown.Toggle>
                                                 <Dropdown.Menu>
                                                        {LANGUAGE_SUPPORTS?.map((lang, index) => <Dropdown.Item key={index} onClick={((userData?.user?.langSelect ?? "") == lang.value) ? null : () => changeAppLanguage(lang.value)}>
                                                               <Image className='me-2 border' src={`${MediaBaseUrl}media/images/flags/${lang.value}.svg`} height={17} width={28} fluid />
                                                               {lang.label}
                                                        </Dropdown.Item>)}
                                                 </Dropdown.Menu>
                                          </Dropdown> : null}
                                          <Dropdown className="head_notifications_ h-100 f-center me-1" align="end" show={isNotification} onToggle={this.onNotificationToggle}>
                                                 <Dropdown.Toggle variant="icon" size='sm' className="fs-6 border-0 arrow_disabled ms-sm-1 position-relative" aria-label='Notifications'>
                                                        {notifications_count ? <span className="notifications_count bg-primary text-white position-absolute rounded-4" id="notificationCount" style={{zIndex:9}}>{notifications_count}</span> : null}
                                                        <i className="fa-duotone fa-solid fa-bell"></i>
                                                 </Dropdown.Toggle>
                                                 <Dropdown.Menu className="p-0">
                                                        <Suspense fallback={<></>}>
                                                               <Notifications notificationList={notificationList} onNotificationToggle={this.onNotificationToggle} />
                                                        </Suspense>
                                                 </Dropdown.Menu>
                                          </Dropdown>
                                          <Suspense fallback={<></>}><UserDropdown navigate={navigate} userData={userData} headerContent={header} /></Suspense>
                                   </div>
                            </div> : null}
                     </div>
                     <UpdateTimeModal show={this.state.upTimeModal} toggleModal={() => this.setState({ upTimeModal: false })} />
              </Navbar>
       }
}

const mapStateToProps = (state) => {
       return {
              notificationList: state.notification.notificationList
       }
}

const mapDispatchToProps = (dispatch) => {
       return {
              updateSalesModal: (data) => dispatch(updateSalesModal(data))
       }
}

export default connect(mapStateToProps, mapDispatchToProps)(Header);